@import "./variables";
@import "./mixins";

*,
*::before,
*::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html{
  height: 100%;
  min-height: 100%;
  font-size: 62.5% !important; // 10px 
}

body {
  font-family: $font-family;
  font-weight: $font-normal;
  color: $text-color;  
  box-sizing: border-box;
  height: 100%;
}

h1,h2,h3,h4,h5,h6,.h1,.h2,.h3,.h4,.h5,.h6{
  font-weight: normal;
  small{
    font-size: 70%;
  }
}

/**
  Visible para tablets y celulares
**/
.small-devices{
  display: none !important;
  @include responsive(tablet){
    display: block !important;
  } 
}

.text-grey {
  color: $color-grey !important;
}

.bigger{
  font-size: 1.2em;
}

.smaller{
  font-size: 0.8rem;
}

.text-success {
  color: $success-color;
}

.text-danger {
  color: $danger-color;
}

.text-warning {
  color: $warning-color;
}

.text-info {
  color: $info-color;
}

.primary-button {
  margin: 1rem;
  padding: 1rem 1.5rem;
  border-color: whitesmoke;
  border-radius: 5px;
  background: $primary-color !important ;
  font-weight: bold;
  color: whitesmoke;
  cursor: pointer;
  letter-spacing: 2px;
  min-width: 18rem;
  text-transform: uppercase;
  transition: .5s;
  animation: ease-in;
  
  &:hover {
    transform: scale(1.1);
  }
}

.full-button {
  width: 100% !important;
  min-width: unset !important;
}